<script>
import FloatLabel from "vue-float-label/components/FloatLabel";
import ModalFormCreatePayLink from '../components/ModalFormCreatePayLink.vue';

import { httpClient } from '../HttpClientV1/http-client'

      const date= new Date();
      import { DotLoader } from '@saeris/vue-spinners'
import  Sidebar from "../components/SideBar.vue";
export default {
 
  components: { FloatLabel,ModalFormCreatePayLink,Sidebar,DotLoader },
  name: "Invoices",
  data() {
    return {
      color: 'rgb(74, 144, 226)',
      size: 90,
      paininvoice: "210",
      unpaininvoice: "65",
      totalinvoice: "520",
      comp: "trans",
      loadedPage:false,
      selected: null,
      selected2: null,
      loadTable: false,
      rows: 0,
      date_in: new Date(date.getFullYear(), date.getMonth(), 2).toISOString().slice(0, 10),
      date_out: new Date().toISOString().slice(0, 10),
      shom_name: null,
      uuid_invoice: null,
      address_wallet: null,

      perPage: 15,
      currentPage: 1,
      options1: [
        {code:'created',label: 'Создан'},
        {code:'paid', label: 'Оплачен'},
        {code:'failed', label: 'Ошибка'},
        {code:'canceled', label: 'Отменен'}
      ],
      options: [],
      tableColumns1: [
        {
          key: "project_uuid",
          label: "Магазин",
          sortable: false,
          class: "table-text-tran1",
        },
        {
          key: "uuid",
          label: "ID Счета",
          sortable: false,
          class: "table-text-tran1",
        },
        {
          key: "currency",
          label: "Валюта приема",
          sortable: true,
          class: "table-text-tran1",
        },
        {
          key: "amount_currency",
          label: "Сумма в валюте",
          sortable: true,
          class: "table-text-tran1",
        },
        {
          key: "amount",
          label: "Сумма в $",
          sortable: true,
          class: "table-text-tran1",
        },
        {
          key: "status",
          label: "Статус",
          sortable: true,
          class: "table-text-tran1",
        },

        {
          key: "created",
          label: "Дата создания",
          sortable: true,
          class: "table-text-tran1",
        },
      ],
      table2: {
        data: [
        ],
      },
    };
  },

  mounted() {
    this.loadedPage= false;
    this.last_invoice();
    this.stat_invoice();
    this.allCurency();

  },
  methods: {
    showResult(){
        this.loadedPage= false;
        this.last_invoice()
        this.stat_invoice()

    },
    last_invoice() {
      try {
        let query =  "date_in="+this.date_in + '&date_out=' + this.date_out
        if (this.uuid_invoice != null){
          query += '&inv_id=' + this.uuid_invoice
        }
        if (this.shop_name != null){
          query += "&proj_name=" + this.shop_name
        }

        if (this.selected2 != null){
          query += "&currency=" + this.selected2.code
        }
        if (this.selected != null){
          query += "&status=" + this.selected.code
        }

        httpClient
          .get("/api/wallet/invoices?"+ query)
          .then(
            (response) => (
              (this.table2.data = response.data, this.loadTable= true, this.rows = this.table2.data.length), console.log(this.table2)
            )
          );
      } catch (e) {
        console.error(e);
      }
    },
    allCurency(){
      httpClient
      .get("/api/wallet/getallcurrency")
      .then((resp)=>(this.options=resp.data))
    },
    stat_invoice() {
      try {

        let query =  "date_in="+this.date_in + '&date_out=' + this.date_out
        httpClient
          .get("/api/wallet/stat_invoice_in_date?"+query)
          .then(
            (response) => (
              ((this.paininvoice = response.data.paid),
              (this.totalinvoice = response.data.total),
              (this.unpaininvoice = response.data.dont_paid),
              (this.procent = response.data.convert),
              (this.balance = response.data.balance),
              (this.loadedPage = true)),
              console.log(this.table2)
            )
          );
      } catch (e) {
        console.error(e);
      }
    },
  },
};
</script>
