<script>
const tableColumns = ["Транзакции", "Дата", "Общее"];
const tableData = [];
import  Sidebar from "./SideBar.vue";
import { DotLoader } from '@saeris/vue-spinners'
import { httpClient } from '../HttpClientV1/http-client'
export default {
  components: {
    Sidebar,
    DotLoader
  },
  name: "Balance",
  data() {
    return {
      color: 'rgb(74, 144, 226)',
      size: 90,
      balance: null,
      comp: "trans",
      value: "",
      rows: 0,
      perPage: 10,
      currentPage: 1,
      data_wallet: null,
      loadedWallet: false,
      loadedPage: false,
      ruleForm: {
        email: null,
        password: null,
      },
      tableColumns1: [
        {
          key: "type",
          label: "Операция",
          sortable: false,
          class: "table-text-tran1",
        },
        {
          key: "amount",
          label: "Сумма в $",
          sortable: true,
          class: "table-text-tran1",
        },

        {
          key: "created",
          label: "Дата",
          sortable: true,
          class: "table-text-tran1",
        },
      ],
      items: [
        { age: 12, Транзакция: "Dickerson", Дата: "Macdonald", Общее: "" },
        { age: 21, Транзакция: "Larsen", Дата: "Shaw", Общее: "" },
        { age: 89, Транзакция: "Geneva", Дата: "Wilson", Общее: "" },
        { age: 38, Транзакция: "Jami", Дата: "Carney", Общее: "" },
      ],
      table1: {
        columns: [...tableColumns],
        data: [...tableData],
      },
    };
  },
  mounted() {
    this.loadedPage = false;
    this.list_wallet();
    this.last_all_invoice();
    this.allBalance();
  },
  methods: {
    showResult() {
      this.loadedPage = false;
      this.last_invoice();
      this.last_all_invoice();
      this.stat_invoice();
    },
    list_wallet() {
      try {
        httpClient
          .get("/api/wallet/getallwallet")
          .then(
            (response) => (
              (this.data_wallet = response.data), (this.loadedWallet = true)
            )
          );
      } catch (e) {
        console.error(e);
      }
    },
    allBalance() {
      httpClient
        .get("/api/wallet/getallbalance")
        .then((resp) => (this.balance = resp.data));
    },
    last_all_invoice() {
      httpClient
        .get("/api/wallet/last_all_invoice")
        .then(
          (resp) => (
            (this.table1.data = resp.data),
            (this.rows = this.table1.data.length),
            (this.loadTable = true),(this.loadedPage = true)
          )
        );
    },
    stat_invoice() {
      try {
        let query = "date_in=" + this.date_in + "&date_out=" + this.date_out;
        httpClient
          .get("/api/wallet/stat_invoice_in_date?" + query)
          .then(
            (response) => (
              ((this.paininvoice = response.data.paid),
              (this.totalinvoice = response.data.total),
              (this.unpaininvoice = response.data.dont_paid),
              (this.procent = response.data.convert),
              (this.balance = response.data.balance),
              (this.loadedPage = true)),
              console.log(this.table2)
            )
          );
      } catch (e) {
        console.error(e);
      }
    },
  },
};
</script>

