<template>
  <b-row class=" d-flex  align-items-center text-center" style="height: 90vh">
    <b-col cols="12" md="6"  lg="6"  offset-md="3"  offset-lg="3" >
      <b-card class="p-4">
        <h1 class="mb-4" >{{ $t("login.Authorization") }}</h1>
        <validation-observer ref="observer" v-slot="{ handleSubmit }">
          <b-form @submit.stop.prevent="handleSubmit(onSubmit)">
            <validation-provider
              name="Email"
              :rules="{ required: true, min: 3 ,email}"
              v-slot="validationContext"
            >
              <b-form-group>
                
                <b-form-input
                 class="input"
                  name="email"
                  v-model="ruleForm.email"
                  placeholder='Email'
                  :state="getValidationState(validationContext)"
                  aria-describedby="input-1-live-feedback"
                ></b-form-input>

                <b-form-invalid-feedback id="input-1-live-feedback">{{
                  validationContext.errors[0]
                }}</b-form-invalid-feedback>
              </b-form-group>
            </validation-provider>
            <validation-provider
              name="Password"
              :rules="{ required: true, min: 6 }"
              v-slot="validationContext"
            >
              <b-form-group>

                <b-form-input
                class="input"
                  :placeholder="$t('login.password')"
                  type="password"
                  v-model="ruleForm.password"
                  :state="getValidationState(validationContext)"
                  autocomplete="off"
                ></b-form-input>
                   <b-form-invalid-feedback id="input-2-live-feedback">{{ validationContext.errors[0] }}</b-form-invalid-feedback>
          
              </b-form-group>
            </validation-provider>
            <b-form-group>
                <div class="row">
                <div class="col d-flex  justify-content-between align-items-center" >
                     <router-link  to="/resetpassword">{{ $t("login.Forgot your password") }}?</router-link>
                    <b-button  type="submit" class="btn btn-primary">{{ $t("login.entrance") }}</b-button>
                </div>
            </div>
            </b-form-group>
               {{ $t("login.You dont have an account") }} ?-
                <router-link to="/registration">{{ $t("login.registration") }}</router-link>
          </b-form>
        </validation-observer>
        <b-row class="d-flex  justify-content-center mt-3">
            <v-select
            class=" input "
            :options="langs"
            :clearable="false"
            v-model="$i18n.locale"
          />
        </b-row>
      </b-card>
    </b-col>
  </b-row>
</template>



<script >
export default {
  name: "Login",
  data() {
    return {
      ruleForm: {
        email: null,
        password: null,
       
      },
       langs: ['Russian', 'English']
    };
  },

  
  methods: {
    getValidationState({ dirty, validated, valid = null }) {
      return dirty || validated ? valid : null;
    },

     switchLang(value) {
       alert(value)
        this.$i18n.locale = this.lang;
      },

    onSubmit() {

      localStorage.removeItem('token')

      localStorage.removeItem('username')
      localStorage.removeItem('email')
      let user = {
        email: this.ruleForm.email,
        password: this.ruleForm.password
      }
       this.$store.dispatch('login', { user })
       .then(() => this.$router.push('/'))
       .catch(err => this.errorLogin = err)
    }
  },
};
</script>
