<template>
  <table class="table">
    <thead>
      <slot name="columns">
        <tr>  
            <th v-for="column in columns" :key="column">{{column}}</th>
        
        </tr>
      </slot>
    </thead>
    <tbody>
    <tr v-for="(item, index) in data" :key="index"  v-on:click="clickCallback(item[0], comp)">


      <slot :row="item">

        <td v-for="(i ,ix) in item" :key="ix" v-html="item[ix]">{{ item[ix] }}</td>
      </slot>
    </tr>
    </tbody>
  </table>
</template>
<script>

  export default {
    name: 'l-table',
    props: {
      columns: Array,
      data: Array,
      comp: String
    },
    methods: {
      hasValue (item, column) {
        return item[column] !== 'undefined'
      },
      itemValue (item, column) {
        console.log(item[column])
        return item[column]
      },
      clickCallback(id, comp){
        if(comp !='None'){
          this.$router.push('/admin/'+comp+"/"+id)
        }
      }
    }
  }
</script>
