<template>
  <b-modal id="OutBalance" ref="vuemodal" :title="$t('payout.Withdrawal request form')" hide-footer>
<b-row>
    <b-col md="12">
        <b-card class="mb-3 bg-1">
          <b-card-text>
            <b-row>
              <b-col md="12">
                <h6 class="white text-center y pr-2">
                  Доступно к выводу
                </h6>
                <div class="d-flex justify-content-center flex-row"  v-if="formOut.wayOfPayoutSelect == 'Crypto'">
                  <h3 class="white">{{ balanceOutCrypto }} </h3> <small  class="align-top ml-2 text-right">  ${{ balanceOutUSD }}</small>
                  
                </div>

                <div class="d-flex justify-content-center flex-row"  v-else-if="formOut.wayOfPayoutSelect == 'Fiat'">
                  <h3 class="white">${{ balanceOutUSD }} </h3><small class="align-top ml-2">  {{ balanceOutCrypto }}</small>
                </div>

                <div class="d-flex justify-content-center flex-row"  v-else>
                  <h3 class="white">${{ balanceOutUSD }} </h3><small class="align-top ml-2">  {{ balanceOutCrypto }}</small>
                  
                </div>
              </b-col>
            </b-row>
          </b-card-text>
        </b-card>
        <b-card class="mb-3  ">
          <b-form :ref="formOut">
            <!-- Способ вывода--->
            <b-form-group class="pb-3">
              <v-select class="input q2" :options="wayOfPayout"  v-model="formOut.wayOfPayoutSelect" @input="clearForm" :clearable="false" placeholder="Способ вывода"/>
            </b-form-group>
            <!--###Способ вывода###--->

            <!--Форма Крипто--->
            <div  v-if="formOut.wayOfPayoutSelect == 'Crypto'">

              <!--Валюта Вывода--->
              <b-form-group class="pb-3">
                <v-select class="input q2" :options="options"  v-model="formOut.selectedCryptoOut" @input="selectCryptoOut" :clearable="false" placeholder="Валюта вывода"/>
              </b-form-group>
              <!--###Валюта Вывода###--->

              <!--Сумма--->
              <float-label :label="$t('payout.Withdrawal amount')" class="mt-3">
                <b-form-group class="pb-3">
                  <b-form-input
                    type='number'
                    class="input"
                    min='10'
                    :max='balanceOut'
                    v-model="formOut.AmountCrypto"
                    :placeholder="$t('payout.Withdrawal amount')"/>
                </b-form-group>
              </float-label>
              <!--###Сумма###--->

              <!--Кошелек--->
              <float-label :label="$t('payout.Wallet')" class="mt-3">
                <b-form-group class="pb-3"
                  v-if="formOut.wayOfPayoutSelect != null">
                  <b-form-input
                    class="input"
                    v-model="formOut.WalletOut"
                    :placeholder="$t('payout.Wallet')"/>
                </b-form-group>
              </float-label>
              <!--###Кошелек###--->

              <!--Комментарий--->
                <float-label :label="$t('payout.Comment')" class="mt-1">
                  <b-form-group >
                    <b-form-textarea
                      v-model="formOut.CryptoComment"
                      class="input pb-2 TApad pt-2"
                      id="textarea-no-resize"
                      :placeholder="$t('payout.Comment')"
                      rows="3"
                      no-resize />
                  </b-form-group>
                </float-label>
              <!--###Комментарий###--->
            </div>
            <!--###Форма Крипто###--->

            <div v-else-if="formOut.wayOfPayoutSelect == 'Fiat'">
              <!--Куда вывод--->
              <b-form-group class="pb-3">
                <v-select class="input q2" :options="optionsOut"  v-model="formOut.selectedOut" :clearable="false" placeholder="Куда вывод"/>
              </b-form-group>
              <!--###Куда вывод###--->

              <!--Откуда вывод--->
              <b-form-group class="pb-3">
                <v-select class="input q2" :options="options"  v-model="formOut.selected2" @input="selectCryptoOut" :clearable="false" placeholder="Вид валюты"/>
              </b-form-group>
              <!--###Откуда вывод###--->

              <!--Сумма--->
              <float-label :label="$t('payout.Withdrawal amount')" class="mt-3">
                <b-form-group class="pb-3">
                  <b-form-input
                    type='number'
                    class="input"
                    min='10'
                    max='20'
                    v-model="formOut.AmountFiat"
                    :placeholder="$t('payout.Withdrawal amount')"/>
                </b-form-group>
              </float-label>
              <!--###Сумма###--->

              <!--Кошелек--->
              <float-label :label="$t('payout.Wallet')" class="mt-3">
                <b-form-group class="pb-3"
                  v-if="formOut.wayOfPayoutSelect != null">
                  <b-form-input
                    class="input"
                    v-model="formOut.WalletOut"
                    :placeholder="$t('payout.Wallet')"/>
                </b-form-group>
              </float-label>
              <!--###Кошелек###--->

              <!--Комментарий--->
                <float-label :label="$t('payout.Comment')" class="mt-1">
                  <b-form-group >
                    <b-form-textarea
                      v-model="formOut.CryptoComment"
                      class="input pb-2 TApad pt-2"
                      id="textarea-no-resize"
                      :placeholder="$t('payout.Comment')"
                      rows="3"
                      no-resize />
                  </b-form-group>
                </float-label>
              <!--###Комментарий###--->
            </div>

            <p>{{ $t("payout.Withdrawal processing time: 2 hours") }}</p>
            <p>{{ $t("payout.Commission amount:") }} 10$</p>
            <p>{{ $t("payout.Total to the conclusion:") }} 990$</p>
            <b-row>
              <b-col class="text-center pt-3">
                <b-form-group>
                  <b-button  @click="sendForm" class="btn  btn-primary mt-2">{{
                    $t("payout.Send a request")
                  }}</b-button>
                </b-form-group>
              </b-col>
            </b-row>
          </b-form>
        </b-card>
      </b-col>
</b-row>
  </b-modal>
</template>
<script>

import { httpClient } from "../HttpClientV1/http-client";
import FloatLabel from "vue-float-label/components/FloatLabel";
export default {
    name:"OutBalance",
    data(){
      return{
        wayOfPayout: [
          'Crypto',
          'Fiat'
        ],
        formOut:
          {
            wayOfPayoutSelect: null,
            selectedCryptoOut: null,
            AmountCrypto: null,
            WalletOut: null,
            CryptoComment: null,
            AmountFiat: null,
            selectedOut: null,
            selected2 : null,

          },
        
        balanceOutUSD: 0,
        balanceOutCrypto: 0,
        options: [],
        optionsOut: [
          'Карта',
          'Кошелек'
        ],
      }
    },
  components: { FloatLabel },
  mounted(){
      this.allCurency();
  },
  methods:{
    selectCryptoOut(value){
        
      httpClient
        .get("/api/wallet/balanceout?currency="+ value.code)
        .then((resp) => (this.balanceOutUSD = resp.data.usd, this.balanceOutCrypto = resp.data.crypto), (this.loadedPage = true));
    },
    clearForm() {
      this.$ref.formOut.reset()
    },
    sendForm(){
      if (this.formOut.wayOfPayoutSelect == 'Crypto'){
        httpClient
        .post("/api/wallet/invoicesout", {data: {'type':this.formOut.wayOfPayoutSelect, 'CurrencyOut': this.formOut.selectedCryptoOut.code, 'AmountOut':this.formOut.AmountCrypto, 'WalletAddressOut': this.formOut.WalletOut, 'Comment': this.formOut.CryptoComment }})
       .then((resp) => {if(resp.data.status == 'ok'){
           this.$notify({
            group: 'foo',
            title: 'Успешно',
            type: 'success',
            text: 'Заявка успешно отправлена. Номер заявки '+ resp.data.data.inv_id
          });
          this.$bvModal.hide('OutBalance')
          this.$parent.last_invoice();
          this.$parent.allCurency();
          this.$parent.stat_invoice();
        }
        else{
          this.$notify({
            group: 'foo',
            title: 'Ошибка',
            type: 'error',
            text: resp.data.data.error
          });
        }
        });
      }
      else if(this.formOut.wayOfPayoutSelect == 'Fiat'){

        httpClient
        .post("/api/wallet/invoicesout", {data: {'type':this.formOut.wayOfPayoutSelect, 'CurrencyOut': this.formOut.selected2.code, 'AmountOut':this.formOut.AmountFiat, 'WalletAddressOut': this.formOut.WalletOut, 'Comment': this.formOut.CryptoComment }})
        .then((resp) => {if(resp.data.status == 'ok'){
           this.$notify({
            group: 'foo',
            title: 'Успешно',
            type: 'success',
            text: 'Заявка успешно отправлена. Номер заявки '+ resp.data.data.inv_id
          });

          this.$bvModal.hide('OutBalance')
          this.$parent.last_invoice();
          this.$parent.allCurency();
          this.$parent.stat_invoice();
        }
        else{
          this.$notify({
            group: 'foo',
            title: 'Ошибка',
            type: 'error',
            text: resp.data.data.error
          });
        }
        });
      }
    },
    allBalance() {
      httpClient
        .get("/api/wallet/getallbalance")
        .then((resp) => (this.balanceOutUSD = resp.data), (this.loadedPage = true));
    },
    allCurency(){
      httpClient
      .get("/api/wallet/getallcurrency")
      .then((resp)=>(this.options=resp.data))
    },
  }
}
</script>